@import "vars";
@import "components/Header/styles";
@import "components/Hero/styles";
@import "components/Contacts/styles";
@import "components/Carousel/styles";

body {
    margin: 0;
    font-family: $font;
    overflow: hidden;
}

section {
    height: 100vh;
    width: 100%;
    padding: 80px 100px;
    box-sizing: border-box;
}

button{
    background: white;
    border: 0;
    padding: 0;
    margin: 0;
}


@media (max-width: 500px) {
    section{
        padding: 24px;
    }
}

@media (max-width: 1200px) {
   body {
       overflow-y: scroll;
   }
}